


.footer {
    border-top:1px solid #D9FFF5;
    padding-top:16px;
    padding-bottom:19px;
}


.footer__copyright {
    display:flex;
    gap:16px;
    flex-direction:column;
}

.footer__copyright-p {
    font-weight:500;
    color:#7E7E82;
    opacity:0.72;
    line-height: 16px;
    letter-spacing: 0px;
    text-align:center;
}



/* *********************768********************* */


@media (min-width:768px) {

    


    .footer {
        padding:32px 24px 25px;
    }
    
    
    .footer__copyright {
        display:flex;
        flex-direction:row;
        justify-content:space-between;
    }
    
    
    .footer__copyright-p:first-child {
        text-align:left;
        min-width:220px;
        height:32px;
    }
    
    .footer__copyright-p:nth-child(2) {
        text-align:left;
        min-width:220px;
        height:32px;
    }
    
    .footer__copyright-p:nth-child(3) {
        text-align:right;
        max-width:213px;
        min-height:48px;
        height:32px;
    }
    
    
    
    
        }



  
/* *********************1120********************* */

    
@media (min-width:1120px) {
       


        

    .footer {
        padding:32px 32px 21px;
    }
    
    
    .footer__copyright-p:nth-child(3) {
        max-width:330px;
    }
    
    
        }       


