.sidebar {
    order:-1;
    position:fixed;
    flex-direction:column;
    justify-content:space-between;
    box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2), -2px 0px 4px rgba(69, 79, 126, 0.02);
    max-width:320px;
    height:100%;
    background:white;

}




.sidebar__header {
    display:flex;
    justify-content:space-evenly;
    width:100%;
    gap:78px;
    padding:18px 20px;
    margin-bottom:32px;
}


.sidebar__button-container {
    display:flex;
    align-items:center;
    gap:16px;
}




.sidebar__list {
    display:flex;
    flex-direction:column;
    margin-left:24px;
    gap:24px;
    
}


.sidebar__link {
    font-family: "TTLakes";
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    text-align: left;
    color:#1B1C21;
    font-weight:500;

}

.sidebar__link--active {
    position:relative;
    color:#7E7E82;
}


.sidebar__link--active::before, .sidebar__link:hover::before{
    content:"";
    position:absolute;
    top:-5px;
    left:-23px;
    width:4px;
    height:32px;
    background-color:#41F6D7;
    border-top-right-radius:5px;
    border-bottom-right-radius:5px;
}

.sidebar__link:hover {
    position:relative;
    color:#7E7E82;
    display:inline-block;
}


.sidebar__main-container {
    padding-bottom:32px;
}


.sidebar__footer {
    margin-left:24px;
}



.sidebar__footer-buttons {
    display:flex;
    justify-content:space-between;
    padding-right:144px;
    padding-bottom:16px;
}



.sidebar__social-links {
    display:flex;
    flex-direction:column;
    align-items:flex-start;
    font-family: "TTLakes";
}

.sidebar__mail {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    font-weight:500;
    padding-bottom:12px;
    font-family:"TTLakes"
}


.sidebar__number {
    padding-bottom:8px;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    font-weight:bold;
    font-family:"TTLakes"
}


.sidebar__number:hover, .sidebar__mail:hover {
    opacity:0.7;
    }


    


    @media (min-width:1120px)   {


        .sidebar {
            box-shadow:none;
            left:auto;
        
        }
        
    
       .sidebar__header {
        justify-content:space-between;
        padding-left:24px;
        padding-right:24px;
       }



       .sidebar__number {
        padding-bottom:40px;
    }



    .sidebar__list {
        gap:32px;
        
    }






    }