

.order-btn  {
    display:flex;
    font-family:"TTLakes";
    height:32px;
    border-radius:15px;
    background-color:#FF3E79;
    justify-content:center;
    align-items:center;
    font-size:12px;
    font-weight:bold;
    color:white;
    }
    
    
    .order-btn:hover {
        transform:scale(1.05);
    }
    
    
    .order-btn--width {
        width:119px;
    }
    
    
    
    .order-btn--position {
        position:absolute;
        bottom:40px;
        right:-225px;
    }
    
    
    
    
    .order-btn__add {
        width:6.8px;
        height:6.8px;
        border-top:2px solid white;
        border-right:2px solid white;
        margin-left:17px;
        transform:rotate(45deg);
    }


    .order-btn--modal-width {
        width:132px;
    }



    /* *********************768********************* */



    @media (min-width:768px) {



        .order-btn--position {
            position:static;
        }




    }





   




