

.section__title {
    background:white;
    font-size:16px;
    padding:16px;
    font-weight:normal;
    color:#7E7E82;
    font-family: "TTLakes";
    line-height: 24px;
    letter-spacing: -0.2px;
    text-align: left;
    border-top:1px solid #D9FFF5;
}






.section__list {
    padding-top:16px;
    padding-left:16px;
    box-sizing:border-box;

  }




  .section__button {
    position:relative;
    display:flex;
    align-items:center;
    justify-content:center;
    width:40px;
    height:40px;
    border:2px solid #FF3E79;
    border-radius:50%;
  }



  .section__button-center {
    position:absolute;
    left:13.5px;
    width:6.5px;
    height:6.5px;
    border:2px solid #FF3E79;
    border-bottom:none;
    border-left:none;
    transform:rotate(45deg);
}


.section__button:hover{
    display:flex;
    align-items:center;
    justify-content:center;
    background-color:#FF3E79;
    width:40px;
    height:40px;
    border:2px solid #FF3E79;
    border-radius:50%;
}


.section__button:hover .section__button-center {
    border:2px solid white;
    border-bottom:#FF3E79;
    border-left:#FF3E79;
}



.section__show-button {
    display:none;
}


/* *********************DEVICE SWIPER********************* */


.section__list-device {
    min-height:228px;
   
  }


.section__dev-info {
    align-self:flex-start;
    font-weight:500;
    font-size:16px;
}

.section__dev-link {
    align-self:flex-end;
}




/* *********************TAABLEEEE****************************************************** */


.section__table-body {
    padding-left:8px;
    padding-top:16px;
}




.section__table-head {
    display:none;
}    


.section__table-row {
position:relative;
display:flex;
flex-direction:column;
align-items:flex-start;
min-width:260px;
max-height:200px;
gap:36px;
padding-top:36px;
padding-left:16px;
padding-bottom:16px;
box-sizing:border-box;

}


.section__table-cell:first-child::before, .section__table-cell:nth-child(2)::before, .section__table-cell:nth-child(3)::before {
    white-space: nowrap;
    font-weight:500;
    font-family:"TTLakes";
    font-size:14px;
    color:#7E7E82;
    opacity:0.7;
}




.section__table-cell:first-child::before {
    content:"Ремонтные услуги";
    position:absolute;
    top:-20px;

}


.section__table-cell:nth-child(2)::before {
    content:"Цена";
    position:absolute;
    top:-20px;

}

.section__table-cell:nth-child(3)::before {
    content:"Срок";
    position:absolute;
    top:-19px;

}



.section__table-cell {
    font-family: "TTLakes";
    font-weight:500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    text-align: left;
    color:#1B1C21;
    position:relative;
}



.section__add-info {
    padding:10px 23px 22px 16px;
    text-align:left;
    line-height: 20px;
    letter-spacing: 0.2px;
}


.section__btn-offer {
    text-align:left;
    font-size:16px;
    font-weight:500;
    margin-left:16px;
    margin-right:112px;
    margin-bottom:40px;
    line-height: 20px;
    letter-spacing: 0.2px;
    
}


.section__btn-offer:hover {
    opacity:0.7;
}



.section__btn-offer-add {
    display:inline-block;
    width:8.1px;
    height:8.1px;
    border-top:2px solid #FF3E79;
    border-right:2px solid #FF3E79;
    margin-left:10px;
    transform:rotate(45deg);

}




/* *********************768**************************************************** */



@media (min-width:768px) {


    .section__title {
        padding-left:24px;
    }


  
 
    ul.section__list-brands {
        max-height:150px;
        transition-property:max-height;
        transition-duration:0.8s; 
    }




    .section__list { 
        overflow:visible; 
        display:flex;             
        flex-wrap:wrap;
        padding-left:24px;
        padding-top:24px;
        padding-bottom:16px;
        padding-right:24px;
        gap:16px 24px;
        margin-bottom:8px;
      
    }


    .section__list--closed {
        max-height:400px  !important;
    }




    .section__show-button {
        position:relative;
       font-family:'TTlakes';
       font-size:16px;
       font-weight:500;
       display:block;
       margin-left:24px;
       padding-left:30px;
       margin-top:10px;
       margin-bottom:32px;
   }
   

   .section__show-button::before {
    position:absolute;
    content:"";
    width:5.6px;
    height:5.6px;
    border:2px solid #41F6D7;
    border-right:none;
    border-top:none;
    transform:rotate(-45deg);
    left:2px;
    top:9px;
}

.section__show-button::after {
    position:absolute;
    content:"";
    width:5.6px;
    height:5.6px;
    border:2px solid #41F6D7;
    border-right:none;
    border-top:none;
    transform:rotate(-45deg);
    left:2px;
    top:2px;
}


.section__show-button.section__show-button--reverse::before {
    position:absolute;
    content:"";
    width:5.6px;
    height:5.6px;
    border:2px solid #41F6D7;
    border-right:none;
    border-top:none;
    transform:rotate(135deg);
    left:2px;
    top:11px;
}



.section__show-button.section__show-button--reverse::after {
    position:absolute;
    content:"";
    width:5.6px;
    height:5.6px;
    border:2px solid #41F6D7;
    border-right:none;
    border-top:none;
    transform:rotate(135deg);
    left:2px;
    top:4px;
}


/* *********************TAABLEEEE****************************************************** */


.section__table {
    display:column;
    margin-top:15px;
    margin-left:24px;
    margin-right:24px;
    
}


.section__table-head {
    display:block;
    padding:0 16px;
    font-weight:500;
} 


.section__table-body {
    display:flex;
    flex-direction:column;
    background-color: #FFFFFF;
    border-radius:6px; 
    padding:0 16px;
    
}



.section__table-cell:first-child {
    min-width:355px;
}


.section__table-cell:nth-child(2) {
    min-width:75px;
}


.section__table-cell:nth-child(3) {
    min-width:85px;
}


.section__table-cell--alpha {
    min-width:119px;
    min-height:13px;
    opacity:0;
    
}




.section__table-cell--grey {
    color:#7E7E82;
    opacity:0.72;  
}



.section__table-row {
    display:flex;
    flex-direction:row;
    align-items:center;
    justify-content:space-between;
    max-height:64px;
    gap:0;
    padding:16px 0;
    border-radius:0 !important;
    border-top:none !important;
    border-left:none !important;
    border-right:none !important;
    box-sizing:border-box;

}

.section__table-row:last-child {
    border-bottom:none;
}




.section__table-cell:first-child::before, 
.section__table-cell:nth-child(2)::before,
 .section__table-cell:nth-child(3)::before {
    display:none;
}



.section__add-info {
    padding:25px 0px 16px 24px;
    text-align:left;
    max-width:450px;

}



.section__btn-offer {
   
    margin-left:24px;
    margin-bottom:47px;
}





}





@media (min-width:1120px) {


    .section__title {
        padding-left:32px;
    }


    ul.section__list {
        padding-left:32px;
        gap:16px 32px;
        overflow: visible
     }


    


     .section__show-button {
         margin-left:33px;
     }


/* *********************DEVICE SWIPER********************* */

.section__show-button {
    margin-bottom:50px;
}



.section__table {
    display:column;
    margin-top:15px;
    margin-left:32px;
    margin-right:32px;
    
}


.section__add-info {
   padding-left:32px;
}


.section__btn-offer {
    margin-left:32px;
    margin-bottom:50px;
}










}
