



.nav {
    overflow:scroll;
    margin-left:16px;
    margin-bottom:24px;
}

.nav__list {
    display:flex;   
}



.nav::-webkit-scrollbar {
    display: none;
}



.nav__link{
    display:inline-block;
    font-size:16px;
    color:#7E7E82;
    font-weight:500;
    letter-spacing: -0.2px;
    white-space:nowrap;
    font-family:"TTLakes";
    padding:8px 20px;
    border:2px solid transparent;
}

.nav__link--active, .nav__link:hover {
    border:2px solid #B8FFEC;
    border-radius:6px;
    background-color:white;
}



@media (min-width:768px) {


    .nav {
        margin-left:24px;
    }



}


@media(min-width:1120px) {


    .nav__list {
        flex-wrap:wrap;
        gap:6px 0;
    }

    
    .nav {
        margin-left:30px;
    }


}



