


.general-model {
    position:relative;
    margin:0 auto;
    max-width:1440px;
   
  

}




.general-model__sidebar {
    z-index:50;
    min-width:320px;
    transition: left 800ms;
    left:-600px;
    top:0px;

}


.general-model__sidebar--open {
    display:flex;
    left:0px;
}




@media(min-width:1120px) {


    .general-model__sidebar {
        max-width:320px;
        display:flex;
        left:auto;
    }



}