


.swiper {
    
    max-width:2800px;
}


.swiper-wrapper {
    
    min-height:115px;
    box-sizing:border-box;
    
    
  }




.swiper .swiper-pagination-bullet {
   
    height: 12px;
    width: 12px;
    background-color: #DDDDDD;

}


.swiper-geometry {
    max-height:72px;
    padding:16px;
    display:flex;
    align-items:center;
    justify-content:space-between;
    
}





 .swiper-slide  {
    box-sizing:border-box;
    border:1px solid #EAEAEA;
    border-radius:6px;
    background:white;
    
}



/* *********************DEVICE SWIPER********************* */


.swiper-device {
    position:relative;
    overflow:hidden;
    

}

.swiper-geometry-device {
   min-height:160px;
   padding:16px;
   display:flex;
   justify-content:space-between
}




.swiper-wrapper-device {
    display:flex;
    width:100%;
    box-sizing:border-box;
    
    
  }




.swiper-device .swiper-pagination-bullet {
   
    height: 12px;
    width: 12px;
    background-color: #DDDDDD;

}

.swiper-device .swiper-pagination {
    position:absolute;
    top:195px;

}



/* *********************TAABLEEEE****************************************************** */


.swiper-services {
    position:relative;
    overflow:hidden;
    width:auto;
    display:flex;

}


.swiper-services-wrapper {
    display:flex;
    width:100%;
    padding-bottom:35px;

    }


.swiper-services .swiper-pagination-bullet {
        height: 12px;
        width: 12px;
        background-color: #DDDDDD;
    }

   
.swiper-services .swiper-pagination {
    position:absolute;
    top:230px;
}





/* *********************768******************************************************** */


@media (min-width:768px) {


    .swiper-geometry {
        max-width:224px;
     }



     /* *********************DEVICE SWIPER********************* */

.swiper-geometry-device {
    max-width:224px;
}


.swiper-device--closed {
    max-height:700px !important;
   

}


div.swiper-device {
    max-height:200px;
    transition-property:max-height;
    transition-duration:0.8s;  

}


.swiper-device--closed {
    min-height:auto ;
   

}


.swiper-device .swiper-pagination {
    display:none;
}


/* *********************TAABLEEEE****************************************************** */







.swiper-services .swiper-pagination {
    display:none;
 }
 
 
 
 
 
 .swiper-services {
     display:flex;
     flex-direction:column;
 }
 





}








/* *********************1120*************************************** */



@media (min-width:1120px) {


    .swiper-geometry {
        max-width:226px ;
      }
     


/* *********************DEVICE SWIPER********************* */

.swiper-geometry-device {
max-width:226px;
}



}






@media (min-width:1440px) {

    .swiper-geometry {
        max-width:240px ;
      }



      .swiper-geometry-device {
        max-width:240px;
    }

}