



.primary-btn__imagebox { 
    display:flex;
    align-items:center;
    justify-content:center; 
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color:#FF3E79;
}


.primary-btn__text {
    display:none;
}

.primary-btn--modal-geometry {
    position:absolute;
    top:-80px;
    left:16px;
    
}



@media (min-width:1120px) {

    


    .primary-btn--close-sidebar {
        display:none;
    }


    .primary-btn--modal-geometry {
        top: -6px;
        left:-65px;
        
    }


    .primary-btn--header-geom {
        display:flex;
        align-items:center;
        gap:8px;
        padding-top:32px;
    }


    .primary-btn__text {
        display:flex;
        font-size:16px;
        font-weight:500;
        font-family:"TTLakes", sans-serif;
    }


   

    
}



