


.section-modal {
    box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2), -2px 0px 4px rgba(69, 79, 126, 0.02);
    position:fixed;
    max-width:320px;
    height:100%;
    background-color:#FFFFFF;
}




.section-modal--callback {
    top:0px;
    right:-600px;
    transition: right 800ms;
    z-index:1000;
    
}



.section-modal--callback-open {
    top:0px;
    right:0px;
    
}


.section-modal__wrapper {
    position:relative;
    padding:0px 16px;
    
}


.section-modal__title {
    position:relative;
    font-family: "TTLakes";
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    text-align: left;
    margin-top:104px;
    margin-bottom:24px;
}


.section-modal__title::before {
  content:"";
  position:absolute;
  top:-6px;
  left:-16px;
  width:4px;
  height:40px;
  background-color:#41F6D7;
  border-top-right-radius:5px;
  border-bottom-right-radius:5px;




}





.section-modal__form {
    display:flex;
    flex-direction:column;
    gap:16px;
}
   

.section-modal__name, .section-modal__phone-number, .section-modal__email, .section-modal__message {
    padding-top:12px;
    padding-bottom:12px;
    padding-left:16px;
    border:1px solid #EAEAEA;
    border-radius:8px;
    color:#B5B6BC;
    font-size:16px;
    font-family:"TTLakes";
    font-weight:500;
    opacity:0.72;
    line-height: 24px;
    letter-spacing: -0.2px;
}


.section-modal__message {
    resize:none;
}




.section-modal__name:focus, .section-modal__phone-number, .section-modal__email, .section-modal__message {
    outline-color:#FF3E79;
}



.section-modal__message {
    height:118px;
}



.section-modal__text {
   font-family:"TTLakes";
   font-size:14px;
   font-weight:500;
   color:#7E7E82;
   line-height: 16px;
   letter-spacing: 0px;
   opacity:0.9;
}



.section-modal__text-a {
    color:#FF3E79;
}


.section-modal__button {
    align-self:flex-end;
}




.section-modal--feedback {
    z-index:1000;
    top:0px;
    right:-600px;
    transition: right 800ms;
}



.section-modal--feedback-open {
    top:0px;
    right:0px;
}






/* 1120///////////////////////////////////////////////////////////////////////////////////////// 1120*/

@media (min-width:1120px) {


    .section-modal {
    
        
        max-width:440px;
    }


    


    .section-modal__title {
        margin-top:40px;
    }




    .section-modal__title::before {
        
        left:-18px;
      }





}