.wrapper {
    max-width:1120px;
    height:auto;
    margin:0px auto;
    background:#F8F8F8;
    position:relative;
}




.wrapper__header {
    display:flex;
    flex-wrap:wrap;
    flex-direction:column;
   
}




@media(min-width:1120px) {

    .wrapper {
        margin-left:315px;
      }


      .wrapper__header {
        
        display:flex;
        flex-wrap:wrap;
        flex-direction:row;
        justify-content:space-between;
        align-items:flex-end;
        margin-bottom:10px;
    }


    
}