



.section-main {
    display:flex;
    flex-direction:column;
    gap:14px;
}


.section-main__content {
    display:flex;
    align-items:flex-start;
    flex-direction:column;
    gap:16px;
}

.section-main__text { 
    transition-property:max-height;
    transition-duration:1.5s;
    display:flex;
    flex-direction:column;
    margin-left:16px;
    margin-right:18px;
    gap:16px;
    overflow:hidden;
    max-height:100px;
    
}


.section-main__full-text {
    transition-property:max-height;
    transition-duration:1.5s;
    max-height:1000px;
    
}




.section-main__text-p {
    text-align:left;
    line-height: 18px;  
    letter-spacing:0.2px;
}


.section-main__readmore-button {
    position:relative;
    background-color:#F8F8F8;
    border:none;
    font-family:"TTLakes";
    font-size:16px;
    font-weight:500;
    letter-spacing: -0.2px;
    margin-left:29px;
    padding-left:16px;
   
}





.section-main__readmore-button::before {
    position:absolute;
    content:"";
    width:5.6px;
    height:5.6px;
    border:2px solid #41F6D7;
    border-right:none;
    border-top:none;
    transform:rotate(-45deg);
    left:-2px;
    top:9px;
}

.section-main__readmore-button::after {
    position:absolute;
    content:"";
    width:5.6px;
    height:5.6px;
    border:2px solid #41F6D7;
    border-right:none;
    border-top:none;
    transform:rotate(-45deg);
    left:-2px;
    top:2px;
}



.section-main__readmore-button--reverse::before {
    transform:rotate(135deg);
    top:11px;
}


.section-main__readmore-button--reverse::after {
    transform:rotate(135deg);
    top:4px;

}






.section-main__imgcontainer {
    width:320px;
    height:210px;
    
    margin-bottom:20px;
}

.section-main__img {
    width:100%;
    height:100%;
}





@media (min-width:768px) {


    .section-main {
        display:flex;
        flex-direction:row;
        gap:54px;
    }
    
    .section-main__content {
        display:flex;
        flex-direction:column;
        gap:10px;
        
    }
    
    
    .section-main__text {
        transition-property:max-height;
        transition-duration:1.5s;
        max-height:157px;
        margin-left:24px;
        gap:20px 0px;
        overflow:hidden;
       
    }
    
    
    .section-main__full-text {
        max-height:1000px;
        transition-property:max-height;
        transition-duration:1.5s;
    };
    
    
    .section-main__text-p {
        text-align:left;
        font-size:14px;
        line-height:20px;
        letter-spacing: 0.2px;
    }
    
    
    .section-main__readmore-button {
        margin-left:32px;
      }
        
        
      .section-main__imgcontainer {
        width: 358px;
        height: 209px;
        margin-top:0;
    }
    
    .section-main__img {
        width:358px;
        height:209px;
        border-radius:6px;
    }



}




@media(min-width:1120px) {


    .section-main {
        gap:46px;
    }

    .section-main--geometry {
        padding-right:10px;
    }


    .section-main__text {
        transition-property:max-height;
        transition-duration:1.5s;
        margin-left:34px;
        max-height:160px;
    }



    .section-main__full-text {
        max-height:1000px;
        transition-property:max-height;
        transition-duration:1.5s;
    }

    



    .section-main__readmore-button {
        margin-left:38px;
      }


    .section-main__imgcontainer {  
        width:358px;
        height:209px;
        margin-right:20px;
    }

    .section-main__img {
        width: 358px;
        height:209px;
        border-radius:10px;
    }


}



@media(min-width:1440px) {


    .section-main__imgcontainer {
        width:500px;
        height:310px;
        margin-right:20px;
    }
    
    .section-main__img {
        width: 500px;
        height:310px;
        border-radius:10px;
    }
    
    
    }





