*, *::after, *::before {
    margin:0;
    padding:0;
    border:none;
    background:none;
    list-style-type:none;
    text-decoration:none;
}

button {
    cursor:pointer;
}


html {
    box-sizing: border-box;
    
  }

  *, *:before, *:after {
    box-sizing: inherit;
  }

  body {
    font-size:14px;
    line-height:1.2;
    font-family:"TTLakes", sans-serif;
    background:white;
}