




.header__menu {
    display:flex;
    border-bottom:1px solid #D9FFF5;
    background-color:white;
    margin-bottom:24px;
    padding:18px 0;
    
}



.header__buttons {
    position:relative;
    width:100%;
    display:flex;
    align-items:center;
    justify-content:space-evenly;
}



.header__buttons:nth-child(2) {
    display:none;
}

.header__buttons:first-child {
    min-width:186px;
}



.header__buttons:last-child::before {
    content:"";
    height: 32px;
    width: 2px;
    background-color:#EAEAEA;
    border-radius: 1px;
    position:absolute;
    left:-2px;

}



.header__text {
    position:relative;

}

.header__title {
    margin-left:15px;
    margin-bottom:24px;
}


.header__text::before {
    position:absolute;
    top:-6px;
    content:"";
    display:flex;
    width:4px;
    height:40px;
    background-color:#41F6D7;
    border-top-right-radius:5px;
    border-bottom-right-radius:5px;
  
}




@media (min-width:768px) {


    .header__buttons:nth-child(2) {
        display:flex;
        margin-left:250px;
    }

    .header__buttons:last-child::before {
        content:"";
        height: 32px;
        width: 2px;
        background-color:#EAEAEA;
        border-radius: 1px;
        position:absolute;
        left:6px;
    }
    
    .header__buttons:first-child .primary-btn {
        margin-left:30px;
        margin-right:40px;
        }  


    .header__buttons:first-child::before {
        content:"";
        height: 32px;
        width: 2px;
        background-color:#EAEAEA;
        border-radius: 1px;
        position:absolute;
        left:calc(50% + -15px);
       }

    .header__title {
        margin-left:18px;
        margin-bottom:32px;
    }   



}




@media(min-width:1120px) {


    .header__menu {
        margin-bottom:0px;
        border-bottom:none;
        padding:0;
    }
    

    .header__buttons {
        background-color:#F8F8F8;
        gap:32px;
    }

    .header__buttons:last-child::before {
        display:none;
    }


    .header__buttons:first-child, .header__buttons:nth-child(2) {
        display:none;
    }



    .header__buttons:last-child .primary-btn:last-child {
        margin-right:32px;
     }

    

    .header__text {
        order:-1;
        position:relative;
    }

    .header__text::before {
        display:none;
          }
      


    .header__text::after {
        position:absolute;
        top:36px;
        content:"";
        display:flex;
        border:1px solid #41F6D7;
        width:4px;
        height:40px;
        background-color:#41F6D7;
        border-top-right-radius:5px;
        border-bottom-right-radius:5px;
    }

    .header__title {
        margin-top:42px;
        margin-left:32px;
        margin-bottom:0px;
    }

    .header__text::before {
  display:none;
    }




}




